/* C64 Style Game Selection Screen */

/* ====== Reset Styles ====== */
html, body, #root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

/* ====== Base Styles ====== */
.launcher-page .c64-container {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #004258;
  padding: 0;
  margin: 0;
  font-family: monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
}

.launcher-page .c64-screen {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  background-color: #00596b;
  border: 20px solid #1d1d1d;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  color: #ffffff;
  position: relative;
  padding: 20px;
  overflow: auto;
  box-sizing: border-box;
}

/* ====== Header Styles ====== */
.launcher-page .c64-header {
  text-align: center;
  margin-bottom: 20px;
}

.launcher-page .c64-header h1 {
  font-size: 28px;
  color: #78f8ff;
  margin: 0 0 10px 0;
  font-weight: bold;
  letter-spacing: 2px;
}

.launcher-page .c64-line {
  height: 3px;
  background-color: #78f8ff;
  margin: 10px 0;
}

/* ====== Content Styles ====== */
.launcher-page .c64-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.launcher-page .c64-intro {
  text-align: center;
  font-size: 18px;
  margin-bottom: 30px;
}

/* .launcher-page .c64-programs-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 30px;
} */

.launcher-page .c64-programs-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 20px;
  margin: 20px 0;
  justify-content: center;
}

.launcher-page .c64-program-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
}

.launcher-page .c64-program-box {
  width: 90px;
  height: 90px;
  background-color: #ffffff;
  border: 8px solid #78f8ff;
  cursor: pointer;
  transition: border-color 0.2s;
  position: relative;
}

.launcher-page .c64-program-box:hover {
  border-color: #ffff00;
}

.launcher-page .c64-program-name {
  margin-top: 10px;
  font-size: 14px;
  text-align: center;
}

/* ====== Program Icons ====== */
.launcher-page .c64-program-icon {
  width: 100%;
  height: 100%;
}

