/* ====== Banner Styles ====== */
.launcher-page .c64-banner {
  background-color: #00354a;
  padding: 10px 20px;
  margin: 20px 0;
}

.launcher-page .c64-banner-instruction {
  color: #78f8ff;
  font-size: 14px;
  margin: 5px 0;
}

.launcher-page .c64-banner-app {
  color: #ffff00;
  font-size: 14px;
  margin: 5px 0;
}

/* ====== Footer Styles ====== */
.launcher-page .c64-footer {
  margin-top: 20px;
}

.launcher-page .c64-ready {
  color: #78f8ff;
  font-size: 18px;
  margin: 10px 0;
  position: relative;
}

.launcher-page .c64-ready::after {
  content: "";
  color: #78f8ff;
  margin-left: 5px;
  animation: blink-cursor 1s infinite;
}

/* ====== Retro Effects ====== */
.launcher-page .c64-scanlines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.05) 50%,
    transparent 100%
  );
  background-size: 100% 4px;
  pointer-events: none;
  z-index: 10;
}

.launcher-page .c64-screen::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    circle at center,
    transparent 60%,
    rgba(0, 0, 0, 0.4) 100%
  );
  pointer-events: none;
  z-index: 5;
}

/* ====== Animations ====== */
@keyframes blink-cursor {
  0%, 49% {
    opacity: 1;
  }
  50%, 100% {
    opacity: 0;
  }
}

/* ====== Responsive Adjustments ====== */
@media (max-width: 768px) {
  .launcher-page .c64-programs-container {
    flex-direction: column;
    align-items: center;
  }
  
  .launcher-page .c64-program-wrapper {
    margin-bottom: 20px;
  }
}

/* CSV Data Viewer icon style */
.icon-csv {
  background-color: #7CFAFF;
  position: relative;
  border-radius: 5px;
}

.icon-csv::before {
  content: '';
  position: absolute;
  top: 25%;
  left: 25%;
  width: 50%;
  height: 25%;
  background-color: #1E3A5B;
  border-radius: 2px;
}

.icon-csv::after {
  content: '';
  position: absolute;
  top: 55%;
  left: 25%;
  width: 50%;
  height: 20%;
  background: linear-gradient(
    to right,
    #1E3A5B 0%, #1E3A5B 20%,
    transparent 20%, transparent 25%,
    #1E3A5B 25%, #1E3A5B 45%,
    transparent 45%, transparent 50%,
    #1E3A5B 50%, #1E3A5B 60%,
    transparent 60%, transparent 65%,
    #1E3A5B 65%, #1E3A5B 85%
  );
}

/* Update the grid layout for 4 items if necessary */
.launcher-page .c64-programs-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 20px;
  margin: 20px 0;
  justify-content: center;
}